div.header{
    margin: 0;
    background: rgb(16, 139, 255);
    width: 100%;
    height: 100px;
    display: flex;

}
.login{
    font-size: 25px;
    font-family: "Trade Winds";
    color:gold;
    text-decoration: none;
    transition: 0.4s;
}
.activeLink{
    color: rgb(255, 123, 0);
}
.login:hover{
    text-decoration: none;
    color: rgb(255, 123, 0);
}
div.loginDiv{
    margin-left: 20%;
    display: flex;
    flex-direction: row;
    align-items: center;
}
div.vertLine{
    width: 100%;
    border-top: gold 4px solid;
}
div.line{
    height: 100%;
    border-left: gold 3px solid;
    margin-right: 60px;
}
div.nameDiv{
    display: flex;
    align-items: center;
    flex-direction: row;
    width: 27%;
}
.name{
    text-decoration: none;
    color: gold;
    font-size: 30px;
    font-family: "Trade Winds";
    position: relative;
    left: 10%;
    transition: 0.4s;
}
div.secondaryFunctions{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 30px 0 20px;
    font-family: "Trade Winds";
    font-size: 25px;
}
.secondary{
    text-decoration: none;
    color:gold;
    transition: 0.3s;
}
.secondary:hover{
    text-decoration: none;
    color: rgb(255, 123, 0);
}
.name:hover{
    text-decoration: none;
    color: rgb(255, 123, 0);
}
